import React from "react"
import axios from "axios"
import ThemeChange from "./ThemeChange.js"
import { FormControl, Button, Input, Grid, GridItem , Tooltip, Box } from "@chakra-ui/react"
import Constants from "../constants.js"

class Form extends React.Component{
    state = {userName: "",msg:"", hidden: true};

    handleSubmit = async event => {
        try{

            event.preventDefault();
            let username = this.state.userName;
    
            const responseUser = await axios.get(`https://api.github.com/users/${username}`)
            .catch(err => {
                if(err.response.status === 404) this.setState({msg:"Could not find user", hidden: false});
                if(err.response.status === 403) this.setState({msg:"Please wait a while before more requests are allowed by github api", hidden: false});
            });

            this.props.onSubmit(responseUser.data);  
            this.setState({userName:""});
            console.log(responseUser.data);
        }
        catch(err){
            console.log(err)
        }          
    }

    onChangeHandler = event => {
        this.setState({userName: event.target.value})
    }
    
    

    render(){
        return(
            <Box className="fixed-top" style={{width: "200px"}}>            
                    <form  className="form-inline " onSubmit={this.handleSubmit}>
                        <p style={{color: "red", marginRight: 10}} onClick={() => {this.setState({msg:"",hidden: true})}} hidden={this.state.hidden}>{this.state.msg}</p>
                        <Input type="search" placeholder="GitHub search user" value={this.state.userName} onChange={this.onChangeHandler} required/>
                        <Tooltip label="Search for any user" fontSize="md">
                        <Button p={4}
                            color="white"
                            fontWeight="bold"
                            borderRadius="md"
                            bgGradient="linear(to-l, #8d769c,#b06cba)" 
                            _hover={{
                                bgGradient:"linear(to-l, #7928CA,#FF0080)",
                            }} mt={4} type="submit">Search
                            </Button>
                        </Tooltip>
                        
                    </form>    
                    
            </Box>            
        );
    }
}

export default Form;